import React, { useState, useEffect, useRef } from "react";
import { useParams, useLocation } from "react-router-dom";
import { Button, Col, Container, Row } from "reactstrap";
import Interweave from "interweave";
import { Tabs } from "antd";

// Core components
import HeaderBreadcrumb from "../components/BreadCrumb";
import WrapContainer from "../components/container";
// APi
import Dataservices from "../services/requestApi";
const ProductDetails = (props) => {
  const mainContent = useRef(null);
  const [data, setData] = useState([]);
  let { id } = useParams();
  const location = useLocation();
  const { TabPane } = Tabs;
  useEffect(() => {
    const getData = async () => {
      try {
        const res = await Dataservices.GetProductById(id);
        console.log(res.data.data);
        setData(res.data.data);
      } catch (e) {
        console.log(e);
      }
    };
    getData();
    return () => {
      getData();
    };
  }, [id]);

  useEffect(() => {
    document.documentElement.scrollTop = 0;
    document.scrollingElement.scrollTop = 0;
    mainContent.current.scrollTop = 0;
  }, [location]);
  return (
    <WrapContainer>
      <HeaderBreadcrumb title="Product details" />
      <div className="product_details" ref={mainContent}>
        <Container fluid>
          <Row>
            <Col lg="5">
              <div className="product_thumb">
                <img
                  src={data.image}
                  className="img-fluid shadow"
                  alt={data.name}
                  loading="lazy"
                />
              </div>
            </Col>
            <Col lg="7">
              <div className="product_desc">
                <h1>{data.name}</h1>
                <Interweave content={data.short_description} tagName="p" />
                <Button color="amber" className="btn-rounded shadow">
                  Add to Cart
                </Button>
              </div>
            </Col>

            <Col lg="12">
              <div className="more_desc">
                <Tabs defaultActiveKey="1" centered>
                  <TabPane tab="Description" key="1">
                    <Interweave content={data.description} tagName="div" />
                  </TabPane>
                  <TabPane tab="Review" key="2">
                    Content of Tab Pane 2
                  </TabPane>
                </Tabs>
              </div>
            </Col>
          </Row>
        </Container>
      </div>
    </WrapContainer>
  );
};
export default ProductDetails;
